


































































import Vue from 'vue';
import { letters_for } from '@/service/letters';
import { thousand } from '@/service/common';


export default Vue.extend({
  data: () => ({
    vocals: [] as any[],
    consonants: [] as any[],
    thousand
  }),
  methods: {
    async loadData() {
      this.vocals = await letters_for('vocal');
      this.consonants = await letters_for('konsonan');
    }
  },
  mounted() {
    this.loadData();
  }
});
